import React from "react";
import clsx from "clsx";
import { push } from "connected-react-router";
import { Chip, Theme, makeStyles } from "@material-ui/core";
import { UniqId } from "../../../_helpers/constants";
import { store } from "../../../_store/store.config";
import { LessonInfo } from "../../../_types";

const useStyles = makeStyles((theme: Theme) => ({
  chip: {
    position: "absolute",
    display: "flex",
    justifyContent: "center",
    flexWrap: "wrap",
    "& > *": {
      margin: theme.spacing(0.5),
    },
    zIndex: 30,
    top: theme.spacing(1),
  },
  chipLeft: {
    right: theme.spacing(1),
  },
  chipRight: {
    left: theme.spacing(1),
  },
  chipInside: {
    backgroundColor: theme.palette.background.default,
    color: theme.palette.secondary.main,
  },
}));

interface CardChipProps {
  labels: LessonInfo | string | string[];
  right?: boolean;
}

export const CardChip: React.FC<CardChipProps> = ({ labels, right }) => {
  const classes = useStyles();
  const clickHandler = (label: string, name: string | number) => {
    store.dispatch(push(`?${label}=${name}`));
  };
  let mapChip: LessonInfo = {};

  if (!labels) {
    return null;
  } else if (typeof labels !== "object") {
    mapChip["tags"] = { id: 1, name: labels };
  } else if (Array.isArray(labels)) {
    labels.forEach((label, i) => {
      mapChip["tags"] = { id: i, name: label };
    });
  } else {
    mapChip = labels;
  }

  return (
    <div
      className={clsx(classes.chip, {
        [classes.chipLeft]: right,
        [classes.chipRight]: !right,
      })}
    >
      {(Object.keys(mapChip) as Array<keyof LessonInfo>).map((label) => {
        const chip =
          label !== "course" && label !== "instrument"
            ? mapChip[label]
            : undefined;
        return (
          chip?.name && (
            <Chip
              size="small"
              key={chip.id + UniqId()}
              label={chip.name}
              className={classes.chipInside}
              onClick={() => clickHandler(label, chip.name)}
            />
          )
        );
      })}
    </div>
  );
};
