import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Link as RouterLink } from "react-router-dom";
import Link from "@material-ui/core/Link";
import { Theme, Collapse } from "@material-ui/core";
import { links } from "./Links";

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    display: "none",
    [theme.breakpoints.up(420)]: {
      marginLeft: theme.spacing(1),
      display: "inline-block",
    },
    [theme.breakpoints.up("sm")]: {
      marginLeft: theme.spacing(2),
      display: "inline-block",
    },
    [theme.breakpoints.up("md")]: {
      marginLeft: theme.spacing(4),
      display: "inline-block",
    },
    [theme.breakpoints.up("lg")]: {
      marginLeft: theme.spacing(8),
      display: "inline-block",
    },
  },
  linkKeeper: {
    overflow: "hidden",
    height: "1.2em",
    lineHeight: "1.2em",
  },
  links: {
    textAlign: "center",
    flexGrow: 1,
  },
}));

interface LinkConstProps {
  openStatus: boolean;
}

const LinkConst = ({ openStatus }: LinkConstProps) => {
  const classes = useStyles();
  return (
    <Collapse in={!openStatus} className={classes.links}>
      <div className={classes.linkKeeper}>
        {links.map((link, i) => (
          <Link
            key={"link-" + i}
            to={link.to}
            className={classes.root}
            component={RouterLink}
            color="secondary"
          >
            {link.title}
          </Link>
        ))}
      </div>
    </Collapse>
  );
};
export default LinkConst;
