import {
  GuideActionTypes,
  DefaultGuideState,
  DefaultGuidesState,
} from "../_types/guide.types";
import { SAVE, REQUEST, SUCCESS, FAILURE } from "../_types";

const guidesReducerDefaultState: DefaultGuidesState = {
  data: [],
  loading: true,
  error: "Not Found",
  nextLoad: 0,
};

export const guidesReducer = (
  state = guidesReducerDefaultState,
  action: GuideActionTypes
): DefaultGuidesState => {
  switch (action.type) {
    case REQUEST.GUIDES:
      return state;
    case SUCCESS.GUIDES:
      return {
        ...state,
        ...action.payload,
        loading: false,
      };
    case SAVE.GUIDES:
      let match = false;
      if (state.data.length)
        match = action.data.every((item) =>
          state.data.map((_) => _.id === item.id)
        );
      return {
        ...state,
        data: match ? state.data : action.data,
      };
    case FAILURE.GUIDES:
      return {
        data: [],
        error: action.error,
        nextLoad: 0,
        loading: false,
      };
    default:
      return state;
  }
};

const guideReducerDefaultState: DefaultGuideState = {
  loading: true,
  error: "Not Found",
};

export const guideReducer = (
  state = guideReducerDefaultState,
  action: GuideActionTypes
): DefaultGuideState => {
  switch (action.type) {
    case REQUEST.GUIDE:
      return { ...state, loading: true, data: undefined };
    case SUCCESS.GUIDE:
      return {
        ...state,
        data: action.data,
        loading: false,
      };
    case FAILURE.GUIDE:
      return {
        ...state,
        error: action.error,
        loading: false,
      };
    default:
      return state;
  }
};
