import {
  LessonActionTypes,
  DefaultLessonState,
  DefaultLessonsState,
} from "../_types/lesson.types";
import { SAVE, REQUEST, SUCCESS, FAILURE } from "../_types";

const lessonsReducerDefaultState: DefaultLessonsState = {
  data: [],
  loading: true,
  error: "Not Found",
  nextLoad: 0,
};

export const lessonsReducer = (
  state = lessonsReducerDefaultState,
  action: LessonActionTypes
): DefaultLessonsState => {
  switch (action.type) {
    case REQUEST.LESSONS:
      return state;
    case SUCCESS.LESSONS:
      return {
        ...state,
        ...action.payload,
        loading: false,
      };
    case SAVE.LESSONS:
      let match = false;
      if (state.data.length)
        match = action.data.every((item) =>
          state.data.map((_) => _.id === item.id)
        );
      return {
        ...state,
        data: match ? state.data : action.data,
      };
    case FAILURE.LESSONS:
      return {
        data: [],
        error: action.error,
        nextLoad: 0,
        loading: false,
      };
    default:
      return state;
  }
};

const lessonReducerDefaultState: DefaultLessonState = {
  loading: true,
  error: "Not Found",
};

export const lessonReducer = (
  state = lessonReducerDefaultState,
  action: LessonActionTypes
): DefaultLessonState => {
  switch (action.type) {
    case REQUEST.LESSON:
      return { ...state, loading: true, data: undefined };
    case SUCCESS.LESSON:
      return {
        ...state,
        data: action.data,
        loading: false,
      };
    case FAILURE.LESSON:
      return {
        ...state,
        error: action.error,
        loading: false,
      };
    default:
      return state;
  }
};
