import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { connect } from "react-redux";

import { AppActions } from "../../_types";
import { IPost } from "../../_types/post.types";
import { AppState } from "../../_types";
import { PostCard } from "./PostCard";
import {
  Chip,
  Container,
  createStyles,
  Grid,
  Theme,
  Typography,
  WithStyles,
  withStyles,
} from "@material-ui/core";
import { LoadingCircle } from "../Layout/Loadings";
import { requestAllPost } from "../../_actions/post.actions";

const useStyles = (theme: Theme) =>
  createStyles({
    root: {
      padding: theme.spacing(2, 1, 0, 2),
    },
    header: {
      position: "relative",
    },
    cardList: {
      minHeight: "calc(100vh - 143px)",
    },
    chip: {
      position: "absolute",
      right: 0,
      top: theme.spacing(1),
    },
    autoMargin: {
      margin: "auto",
    },
    footer: {
      borderTop: `1px solid ${theme.palette.divider}`,
      marginTop: theme.spacing(8),
      paddingTop: theme.spacing(3),
      paddingBottom: theme.spacing(3),
      [theme.breakpoints.up("sm")]: {
        paddingTop: theme.spacing(6),
        paddingBottom: theme.spacing(6),
      },
    },
    namad: {
      cursor: "pointer",
    },
  });

interface PostsPorps extends WithStyles<typeof useStyles> {
  data: IPost[];
  error: string;
  loading: boolean;
}

type Props = PostsPorps & ReturnType<typeof mapDispatchToProps>;

export class PostList extends Component<Props> {
  componentDidMount() {
    this.props.requestBank();
  }

  render() {
    const { loading, data, error, classes } = this.props;
    const length = data ? data.length : 0;
    return (
      <div className={classes.root}>
        <div className={classes.header}>
          <Typography variant="h3"> </Typography>
          <Chip
            className={classes.chip}
            color="secondary"
            label={": " + length}
          />
        </div>
        <Grid container justify="center" className={classes.cardList}>
          {loading ? (
            <LoadingCircle />
          ) : data.length ? (
            data.map((post: IPost) => <PostCard key={post.id} post={post} />)
          ) : (
            <Typography component="div" className={classes.autoMargin}>
              {error}
            </Typography>
          )}
          {/* Footer */}
          <Container
            maxWidth="md"
            component="footer"
            className={classes.footer}
          >
            <Grid container spacing={4} justify="space-evenly">
              <Grid item xs={6} sm={3}>
                <Typography variant="h6" color="textPrimary" gutterBottom>
                   
                </Typography>
                <a href="https://trustseal.enamad.ir/?id=195852&amp;Code=t2sG45wmVWgNnWQcW8qj">
                  <img
                    src="https://Trustseal.eNamad.ir/logo.aspx?id=195852&amp;Code=t2sG45wmVWgNnWQcW8qj"
                    alt=""
                    className={classes.namad}
                    id="t2sG45wmVWgNnWQcW8qj"
                  />
                </a>
              </Grid>
              <Grid item xs={6} sm={3}>
                <Typography variant="h6" color="textPrimary" gutterBottom>
                   
                </Typography>
                <img
                  className={classes.namad}
                  width="100"
                  src="https://cdn.zarinpal.com/badges/trustLogo/1.svg"
                  alt="  "
                />
              </Grid>
            </Grid>
          </Container>
          {/* End footer */}
        </Grid>
      </div>
    );
  }
}

const mapStateToProps = (state: AppState, ownProps: Props): PostsPorps => ({
  classes: ownProps.classes,
  ...state.bank.posts,
});

const mapDispatchToProps = (dispatch: ThunkDispatch<any, any, AppActions>) => ({
  requestBank: bindActionCreators(requestAllPost, dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(useStyles)(PostList));
