import {
  DefaultSearchState,
  SearchActionTypes,
  CLEAN_FILTERLIST,
  FILTERLIST,
  SEARCHING,
} from "../_types/search.types";

const searchReducerDefaultState = {
  searching: "",
  filterList: {
    posts: {},
    guides: {},
    courses: {},
    lessons: {},
    exercises: {},
  },
};

export const searchReducer = (
  state = searchReducerDefaultState,
  action: SearchActionTypes
): DefaultSearchState => {
  switch (action.type) {
    case SEARCHING:
      return {
        ...state,
        searching: action.payload,
      };
    case FILTERLIST:
      return {
        ...state,
        filterList: { ...state.filterList, ...action.payload },
      };
    case CLEAN_FILTERLIST:
      return {
        ...state,
        filterList: {
          guides: {},
          courses: {},
          lessons: {},
          exercises: {},
        },
      };
    default:
      return state;
  }
};
