import React from "react";
import { push } from "connected-react-router";
import {
  Card,
  Button,
  CardMedia,
  Typography,
  CardContent,
  CardActions,
  CardActionArea,
} from "@material-ui/core";
import { makeStyles, Theme } from "@material-ui/core/styles";

import { store } from "../../_store/store.config";
import { IPost } from "../../_types/post.types";
import { thumbnailsFinder } from "../../_helpers/constants";
import { CardChip } from "../Layout/Inputs/CardChip";

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    width: 300,
    maxHeight: 360,
    position: "relative",
    margin: theme.spacing(2),
    [theme.breakpoints.down(350)]: {
      margin: theme.spacing(1),
    },
  },
  cardAction: {
    justifyContent: "space-between",
    margin: "inherit",
  },
  content: {
    minHeight: "140px",
  },
}));

type Props = {
  post: IPost;
};

export const PostCard: React.FC<Props> = ({ post }) => {
  const classes = useStyles();
  const { title, thumbnails, description, teacher, categories } = post;

  const clickHandler = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ): void => {
    store.dispatch(push(`/post/${post.id}`));
  };
  return (
    <Card className={classes.root}>
      <CardChip labels={categories} />
      <CardActionArea onClick={clickHandler}>
        <CardMedia
          height="140"
          component="img"
          alt={post.title}
          title={post.title}
          image={thumbnailsFinder(thumbnails)}
        />
        <CardContent className={classes.content}>
          <Typography gutterBottom variant="h5" component="h2">
            {title}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {description}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions className={classes.cardAction}>
        <Typography variant="body2" color="textSecondary" component="p">
           {teacher}
        </Typography>
        <Button size="medium" color="secondary" onClick={clickHandler}>
           
        </Button>
      </CardActions>
    </Card>
  );
};
