import { BankPages } from ".";

export const SEARCHING = "SEARCHING";
export const FILTERLIST = "FILTERLIST";
export const CLEAN_FILTERLIST = "CLEAN_FILTERLIST";

export interface DefaultSearchState {
  searching: string;
  filterList: IQuery;
}
export type IQuery = {
  [key in keyof typeof BankPages]: { [key: string]: (string | number)[] };
};
export interface SearchingAction {
  type: typeof SEARCHING;
  payload: string;
}
export interface FilterListfoAction {
  type: typeof FILTERLIST;
  payload: IQuery;
}
export interface CleanFilterAction {
  type: typeof CLEAN_FILTERLIST;
}

export type SearchActionTypes =
  | SearchingAction
  | FilterListfoAction
  | CleanFilterAction;
