import { CallHistoryMethodAction, RouterState } from "connected-react-router";

import { DefaultUserState, UserActionTypes } from "./auth.types";
import { SnackActionTypes, DefaultSnackState } from "./snackbar.types";
import { ProfileActionTypes, DefaultProfileState } from "./profile.types";
import {
  PostActionTypes,
  DefaultPostsState,
  DefaultPostState,
} from "./post.types";
import { GuideActionTypes, DefaultGuideState } from "./guide.types";
import { CourseActionTypes, DefaultCourseState } from "./course.types";
import {
  LessonActionTypes,
  DefaultLessonState,
  DefaultLessonsState,
} from "./lesson.types";
import { ExerciseActionTypes, DefaultExerciseState } from "./exercise.types";
import { IMelodition, PlayerActionTypes } from "./player.types";
import { DefaultSearchState, SearchActionTypes } from "./search.types";

export const REQUEST = {
  POST: "REQUEST_POST",
  GUIDE: "REQUEST_GUIDE",
  COURSE: "REQUEST_COURSE",
  LESSON: "REQUEST_LESSON",
  EXERCISE: "REQUEST_EXERCISE",
  POSTS: "REQUEST_POSTS",
  GUIDES: "REQUEST_GUIDES",
  COURSES: "REQUEST_COURSES",
  LESSONS: "REQUEST_LESSONS",
  EXERCISES: "REQUEST_EXERCISES",
} as const;
export const SUCCESS = {
  POST: "SUCCESS_POST",
  GUIDE: "SUCCESS_GUIDE",
  COURSE: "SUCCESS_COURSE",
  LESSON: "SUCCESS_LESSON",
  EXERCISE: "SUCCESS_EXERCISE",
  POSTS: "SUCCESS_POSTS",
  GUIDES: "SUCCESS_GUIDES",
  COURSES: "SUCCESS_COURSES",
  LESSONS: "SUCCESS_LESSONS",
  EXERCISES: "SUCCESS_EXERCISES",
} as const;
export const FAILURE = {
  POST: "FAILURE_POST",
  GUIDE: "FAILURE_GUIDE",
  COURSE: "FAILURE_COURSE",
  LESSON: "FAILURE_LESSON",
  EXERCISE: "FAILURE_EXERCISE",
  POSTS: "FAILURE_POSTS",
  GUIDES: "FAILURE_GUIDES",
  COURSES: "FAILURE_COURSES",
  LESSONS: "FAILURE_LESSONS",
  EXERCISES: "FAILURE_EXERCISES",
} as const;
export const SAVE = {
  POSTS: "SAVE_POSTS",
  GUIDES: "SAVE_GUIDES",
  COURSES: "SAVE_COURSES",
  LESSONS: "SAVE_LESSONS",
  EXERCISES: "SAVE_EXERCISES",
} as const;

export const guides = {
  request: REQUEST.GUIDES,
  success: SUCCESS.GUIDES,
  failure: FAILURE.GUIDES,
  api: "/lesson/v2/guide",
};

export const courses = {
  request: REQUEST.COURSES,
  success: SUCCESS.COURSES,
  failure: FAILURE.COURSES,
  api: "/lesson/v2/course",
};

export const lessons = {
  request: REQUEST.LESSONS,
  success: SUCCESS.LESSONS,
  failure: FAILURE.LESSONS,
  api: "/lesson/v2?course=123",
};

export const exercises = {
  request: REQUEST.EXERCISES,
  success: SUCCESS.EXERCISES,
  failure: FAILURE.EXERCISES,
  api: "/lesson/v2?course=125",
};

export const BankPages = {
  guides,
  courses,
  lessons,
  exercises,
};

export interface ICardDetail {
  id: number;
  slug: string;
  title: string;
  content: string;
  teacher: string;
  info: LessonInfo | LessonInfo2;
  thumbnails: string;
  description: string;
  access_level: string;
  guide_select?: GuideSelect[];
  course_select?: CourseSelect[];
}
export interface LessonInfo {
  tags?: infoDetails;
  genre?: infoDetails;
  grade?: infoDetails;
  course?: infoDetails;
  series?: infoDetails;
  instrument?: infoDetails;
}
export interface LessonInfo2 {
  tags: infoDetails;
  genre: infoDetails;
  grade: infoDetails;
  course: infoDetails;
  series: infoDetails;
  instrument: infoDetails;
}
export interface CourseSelect {
  id: number;
  slug: string;
  title: string;
  content: string;
  teacher: string;
  info: LessonInfo2;
  thumbnails: string;
  description: string;
  access_level: string;
}
export type GuideSelect = {
  exercise_description: string;
  exercise: CourseSelect[];
  course_description: string;
  course: CourseSelect[];
};
export type ThumbnailsInfo = {
  thumbnail: string;
  medium: string;
  medium_large: string;
  large: string;
  "1536x1536": string;
  "2048x2048": string;
};

type infoDetails = {
  name: string | number;
  id: number;
  img?: string;
  color?: string;
  description?: string;
};

export type AppActions =
  | PostActionTypes
  | UserActionTypes
  | GuideActionTypes
  | SnackActionTypes
  | LessonActionTypes
  | CourseActionTypes
  | SearchActionTypes
  | PlayerActionTypes
  | ProfileActionTypes
  | ExerciseActionTypes
  | CallHistoryMethodAction;

export type BankType = {
  posts: DefaultPostsState;
  guides: DefaultLessonsState;
  courses: DefaultLessonsState;
  lessons: DefaultLessonsState;
  exercises: DefaultLessonsState;
};
export type SingleType = {
  post: DefaultPostState;
  guide: DefaultGuideState;
  course: DefaultCourseState;
  lesson: DefaultLessonState;
  exercise: DefaultExerciseState;
};

export type AppState = {
  bank: BankType;
  single: SingleType;
  router: RouterState;
  auth: DefaultUserState;
  melodition: IMelodition;
  user: DefaultProfileState;
  search: DefaultSearchState;
  snackbar: DefaultSnackState;
};
