import {
  ExerciseActionTypes,
  DefaultExerciseState,
  DefaultExercisesState,
} from "../_types/exercise.types";
import { SAVE, REQUEST, SUCCESS, FAILURE } from "../_types";

const exercisesReducerDefaultState: DefaultExercisesState = {
  data: [],
  loading: true,
  error: "Not Found",
  nextLoad: 0,
};

export const exercisesReducer = (
  state = exercisesReducerDefaultState,
  action: ExerciseActionTypes
): DefaultExercisesState => {
  switch (action.type) {
    case REQUEST.EXERCISES:
      return state;
    case SUCCESS.EXERCISES:
      return {
        ...state,
        ...action.payload,
        loading: false,
      };
    case SAVE.EXERCISES:
      let match = false;
      if (state.data.length)
        match = action.data.every((item) =>
          state.data.map((_) => _.id === item.id)
        );
      return {
        ...state,
        data: match ? state.data : action.data,
      };
    case FAILURE.EXERCISES:
      return {
        data: [],
        error: action.error,
        nextLoad: 0,
        loading: false,
      };
    default:
      return state;
  }
};

const exerciseReducerDefaultState: DefaultExerciseState = {
  loading: true,
  error: "Not Found",
};

export const exerciseReducer = (
  state = exerciseReducerDefaultState,
  action: ExerciseActionTypes
): DefaultExerciseState => {
  switch (action.type) {
    case REQUEST.EXERCISE:
      return { ...state, loading: true, data: undefined };
    case SUCCESS.EXERCISE:
      if (action.data === undefined) {
        return {
          ...state,
          data: action.data,
          loading: false,
        };
      } else {
        return {
          ...state,
          data: action.data,
          loading: false,
        };
      }
    case FAILURE.EXERCISE:
      return {
        ...state,
        error: action.error,
        loading: false,
      };
    default:
      return state;
  }
};
