import { Dispatch } from "redux";
import { REQUEST, SUCCESS, FAILURE, AppState } from "../_types";
import { API } from "../_helpers/api";
import { AppActions } from "../_types";
import { errorHandler } from "../components/Layout/SnackBar/alert";
import { getTheTime } from "../_helpers/constants";

export const requestAllPost = () => (
  dispatch: Dispatch<AppActions>,
  getState: () => AppState
) => {
  const postState = getState().bank.posts;
  const currentTime = getTheTime();
  const resetTime = currentTime - postState.nextLoad;
  if (postState.data && resetTime <= 0) {
    return;
  }
  dispatch({ type: REQUEST.POSTS });

  API.get(`/post/v2`)
    .then((res) =>
      dispatch({
        type: SUCCESS.POSTS,
        payload: {
          data: res.data,
          nextLoad: getTheTime(10),
        },
      })
    )
    .catch((err) => errorHandler(err, FAILURE.POSTS));
};

export const requestPost = (id: number) => (
  dispatch: Dispatch<AppActions>,
  getState: () => AppState
) => {
  const postState = getState().bank.posts.data;

  const found = postState?.find((post) => post.id === id);
  if (found) {
    dispatch({ type: SUCCESS.POST, data: found });
  } else {
    dispatch({ type: REQUEST.POST });
    API.get(`/post/v2?id=${id}`)
      .then((res) => dispatch({ type: SUCCESS.POST, data: res.data[0] }))
      .catch((err) => errorHandler(err, FAILURE.POST));
  }
};
