import React from "react";
import { Link as RouterLink } from "react-router-dom";
import Link from "@material-ui/core/Link";
import {
  Grid,
  makeStyles,
  Theme,
  Typography,
  useTheme,
} from "@material-ui/core";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import CssBaseline from "@material-ui/core/CssBaseline";
import Divider from "@material-ui/core/Divider";
import {
  Brightness7,
  Brightness4,
  ChevronLeft,
  ChevronRight,
} from "@material-ui/icons";
import IconButton from "@material-ui/core/IconButton";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import { links, sideLink } from "./Links";
import { AppBarInterface } from "./MainAppBar";
import { AntSwitch } from "../Inputs/AntSwitch";

const drawerWidth = 240;

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    display: "flex",
  },
  hide: {
    display: "none",
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    display: "flex",
    alignItems: "center",
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: "flex-start",
  },
  toolbar: {
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
    padding: theme.spacing(0, 1, 0, 4),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
  },
}));

export default function MiniDrawer({
  state,
  loggedIn,
  theme,
  changeTheme,
  dispatch,
}: AppBarInterface) {
  const classes = useStyles();
  const { direction } = useTheme();
  const handleDrawerClose = () => {
    dispatch({ type: "menu-toggle" });
  };

  const handleThemeChanger = () => {
    if (theme === "light") changeTheme("dark");
    else changeTheme("light");
  };

  const botLinks = loggedIn ? sideLink.in : sideLink.out;
  return (
    <div className={classes.root}>
      <CssBaseline />
      <Drawer
        className={classes.drawer}
        variant="temporary"
        anchor={direction === "ltr" ? "right" : "left"}
        open={state.menuIsOpen}
        onClose={handleDrawerClose}
        classes={{
          paper: classes.drawerPaper,
        }}
      >
        <div className={classes.toolbar}>
          <Typography component="div">
            <Grid component="label" container alignItems="baseline" spacing={1}>
              <Grid item>
                <Brightness7 />
              </Grid>
              <Grid item>
                <AntSwitch
                  checked={theme === "dark"}
                  onChange={handleThemeChanger}
                  name="checkedC"
                />
              </Grid>
              <Grid item>
                <Brightness4 />
              </Grid>
            </Grid>
          </Typography>
          <IconButton onClick={handleDrawerClose}>
            {direction === "ltr" ? <ChevronLeft /> : <ChevronRight />}
          </IconButton>
        </div>
        <Divider />
        <List>
          {links.map((text, index) => (
            <Link
              to={text.to}
              key={"sidelink-" + index}
              onClick={handleDrawerClose}
              component={RouterLink}
              color="secondary"
            >
              <ListItem button>
                <ListItemIcon>{text.icon}</ListItemIcon>
                <ListItemText primary={text.title} />
              </ListItem>
            </Link>
          ))}
        </List>
        <Divider />
        <List>
          {botLinks.map((item, index) => (
            <Link
              to={item.to}
              key={"sidelink-" + index}
              onClick={handleDrawerClose}
              component={RouterLink}
              color="secondary"
            >
              <ListItem button>
                <ListItemIcon>{item.icon}</ListItemIcon>
                <ListItemText primary={item.name} />
              </ListItem>
            </Link>
          ))}
        </List>
      </Drawer>
    </div>
  );
}
