import {
  PostActionTypes,
  DefaultPostState,
  DefaultPostsState,
} from "../_types/post.types";
import { REQUEST, SUCCESS, FAILURE } from "../_types";

const postsReducerDefaultState: DefaultPostsState = {
  data: [],
  loading: true,
  error: "Not Found",
  nextLoad: 0,
};

export const postsReducer = (
  state = postsReducerDefaultState,
  action: PostActionTypes
): DefaultPostsState => {
  switch (action.type) {
    case REQUEST.POSTS:
      return state;
    case SUCCESS.POSTS:
      return {
        ...state,
        ...action.payload,
        loading: false,
      };
    case FAILURE.POSTS:
      return {
        data: [],
        error: action.error,
        nextLoad: 0,
        loading: false,
      };
    default:
      return state;
  }
};

const postReducerDefaultState: DefaultPostState = {
  loading: true,
  error: "Not Found",
};

export const postReducer = (
  state = postReducerDefaultState,
  action: PostActionTypes
): DefaultPostState => {
  switch (action.type) {
    case REQUEST.POST:
      return { ...state, loading: true, data: undefined };
    case SUCCESS.POST:
      if (action.data === undefined) {
        return {
          ...state,
          data: action.data,
          loading: false,
        };
      } else {
        return {
          ...state,
          data: action.data,
          loading: false,
        };
      }
    case FAILURE.POST:
      return {
        ...state,
        error: action.error,
        loading: false,
      };
    default:
      return state;
  }
};
