import React from "react";
import { CircularProgress, makeStyles, Theme } from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) => ({
  loading: {
    display: "flex",
    height: "100%",
    width: "100vw",
    boxShadow: "inset 0 1px 4px 0px black",
  },
  loadingPad: {
    display: "flex",
    width: "100vw",
    minHeight: "calc(100vh - 300px)",
    overflow: "hidden",
  },
  absolute: {
    display: "flex",
    height: "100%",
    width: "100%",
    position: "absolute",
    boxShadow: "inset 0 1px 4px 0px black",
    zIndex: 5,
    backgroundColor: theme.palette.background.paper,
  },
}));

interface LoadingProps {
  noPad?: boolean;
  absolute?: boolean;
}

export const LoadingCircle: React.FC<LoadingProps> = ({ noPad, absolute }) => {
  const classes = useStyles();
  return (
    <div
      className={
        noPad
          ? classes.loading
          : absolute
          ? classes.absolute
          : classes.loadingPad
      }
    >
      <CircularProgress color="secondary" style={{ margin: "auto" }} />
    </div>
  );
};
